@use 'variables' as var;

.btn{
  width: 8rem;
  height: 2.5rem;
  margin: .5rem auto;
  border: none;
  outline: none;
  border-radius: .2rem;
  font-family: inherit;
  font-size: var(--h3-font-size);
  font-weight: var.$font-medium;
  transition: all .4s ease-in;
  cursor: pointer;
  box-shadow: 0px 0px 10px 5px hsl(215, 21, 10);
}

.btn:hover{
  filter: brightness(120%);
}

.btn:active{
  transform: scale(.90);
}

.btn--add{
  background: hsla(183, 99, 36);
}

.btn--delete{
  background: hsl(0, 70, 60);
}

