@use "./variables" as var;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  background: var.$primary-color;
  font-family: var.$body-font;
  position: relative;
}

img {
  max-width: 100%;
}

h1 {
  font-size: var(--h1-font-size);
  color: var.$text-color;
}

h2 {
  font-size: var(--h2-font-size);
}

p {
  font-size: var(--normal-font-size);
}

h1,
h2,
h3,
p {
  color: var.$text-color;
}
