@use './variables' as var;

.viewer{
  width: max(18rem, 90%);
  border-radius: .2rem;
  background: var.$secondary-alt-color;

  .viewer__header{
    height: 6rem;
    background: var.$secondary-color;
    padding: .5rem 1rem;

    h2,h3{
      color: var.$text-color;
    }

    h2{
      text-transform: uppercase;
    }

    h3{
      font-weight: var.$font-regular;
    }

    p{
      color: var.$text-color;
      font-style: italic;
    }

  }

  .viewer__body{
    height: 40rem;
    padding: .5rem 1rem;
    display: flex;
    justify-content: space-between;

    .viewer__content{
      max-width: 15rem;
      .field{
	display: flex;

	.field__data,
	.field__years{
	  flex: 1;
	}

      }
    }
  }

  .viewer__aside{
    display: flex;
    height: 100%;
    flex-direction: column;
    gap: 5rem;
    .viewer__info{
    }
  }
}


h2,h3,p{
  color: var.$primary-color;
  overflow-wrap: break-word;
}
